.content-result {
  color: $green;
  padding: 20px;

  .content-result-title-wrapper {
    height: 650px;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
    @include media("s") {
      height: 350px;

    }
    h1 {
      color: $green;
      margin: 30px 0;
      font-size: 40px;
      font-weight: 100;
      line-height: 1.76;
      @include media("s") {
        font-size: 24px;
      }
    }
    span {
      position: relative;

      width: 1px;
      border-left: 3px solid $white;
      &:first-child {
        flex: 3;
        border-color: $green;
        &:after {
          position: absolute;
          left: -7px;
          top: 100%;
          content: '';
          display: block;
          width: 11px;
          height: 11px;
          border-radius: 50%;
          background: $green;
        }
      }
      &:last-child {
        flex: 2;
        border-color: $green;
        &:before {
          position: absolute;
          left: -7px;
          bottom: 100%;
          content: '';
          display: block;
          width: 11px;
          height: 11px;
          border-radius: 50%;
          background: $green;
        }
      }
    }
  }
}

