.statge-wrapper {
  width: 100%;
  position: relative;
}

.statge {
  .title {
    span {
      border-color: $green;
    }
    h1 {
      color: $green;
    }
  }
  .question {
    .options {
      li {
        border: 2px solid rgba($green, 0.5);
      }
    }
  }
}



